import stars from '@/components/stars/stars'
import lineTitle from '@/components/header/line_title'
import preferential from '@/components/preferential/preferential'
export default {
  props: {
    seller: {}
  },
  components: {
    stars,
    lineTitle,
    preferential
  },
  data () {
    return {
      isDetailShow: false
    }
  },
  methods: {
    showDetail () {
      this.isDetailShow = true
    },
    closeDetail () {
      this.isDetailShow = false
    }
  }
}



