export default {
  props: {
    type: {},
    size: {},
    oppo: {}
  },
  data () {
    return {
      logo_type: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
      logo_size: {small: 'small', medium: 'medium', large: 'large'}
    }
  },
  computed: {
    isOppo: function () {
      if (this.oppo) {
        return ['decrease_oppo', 'discount_oppo', 'special_oppo', 'invoice', 'guarantee']
      } else {
        return this.logo_type
      }
    }
  }
}



