import split from '@/components/split/split'
import cartControl from '@/components/cartControl/cartControl'
import BetterScroll from 'better-scroll'
export default {
  props: {
    selectedFood: {}
  },
  components: {
    split,
    cartControl
  },
  data () {
    return {
      isShow: false,
      food_warpper: {},
      isContent: true,
      isGood: false,
      isBad: false,
      isAll: true
    }
  },
  methods: {
    show_food () {
      this.isShow = true
    },
    _close () {
      this.isShow = false
      this._init_data()
    },
    _toggleIsContent () {
      this.isContent = !this.isContent
    },
    _add_cart () {
      if (!this.selectedFood.count) {
        this.$set(this.selectedFood, 'count', 1)
      } else {
        this.selectedFood.count++
      }
    },
    _toggle_all () {
      this.isAll = !this.isAll
      this.isGood = false
      this.isBad = false
    },
    _toggle_good () {
      this.isGood = !this.isGood
      this.isAll = false
      this.isBad = false
    },
    _toggle_bad () {
      this.isBad = !this.isBad
      this.isAll = false
      this.isGood = false
    },
    _init_data () {
      this.isContent = true
      this.isGood = false
      this.isBad = false
      this.isAll = true
    },
    timeFormat (time) {
      var date = new Date(time)
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      if (month < 10) {
        month = '0' + month
      }
      var day = date.getDate()
      if (day < 10) {
        day = '0' + day
      }
      var hour = date.getHours()
      var minute = date.getMinutes()
      return `${year}-${month}-${day} ${hour}:${minute}`
    }
  },
  mounted () {
    this.food_warpper = new BetterScroll(this.$refs['food-warpper'], {click: true})
  },
  computed: {
    good_ratings () {
      return this.selectedFood.ratings.filter((rating) => {
        return rating.rateType === 0
      })
    },
    bad_ratings () {
      return this.selectedFood.ratings.filter((rating) => {
        return rating.rateType === 1
      })
    },
    all_ratings () {
      if (this.isContent) {
        if (this.isAll) {
          return this.selectedFood.ratings.filter((rating) => {
            return rating.text
          })
        }
        if (this.isGood) {
          return this.good_ratings.filter((rating) => {
            return rating.text
          })
        }
        if (this.isBad) {
          return this.bad_ratings.filter((rating) => {
            return rating.text
          })
        }
      } else {
        if (this.isAll) {
          return this.selectedFood.ratings.filter((rating) => {
            return rating
          })
        }
        if (this.isGood) {
          return this.good_ratings.filter((rating) => {
            return rating
          })
        }
        if (this.isBad) {
          return this.bad_ratings.filter((rating) => {
            return rating
          })
        }
      }
    }
  }
}



