
import cartControl from '@/components/cartControl/cartControl'
import BetterScroll from 'better-scroll'
import Confirm from 'vux/src/components/confirm/index.vue'
import Alert from 'vux/src/components/alert/index.vue'


export default {
props: {
foodList: {},
seller: {}
},
components: {
cartControl,
Confirm,
Alert
},
data () {
return {
cartScroll: {},
isShow: false,
confirmShow: false,
alertShow: false
}
},
computed: {
total_count: function () {
var count = 0
this.foodList.forEach(food => {
if (food.count) {
count += food.count
}
})
return count
},
total_price: function () {
var price = 0
this.foodList.forEach(food => {
if (food.count) {
price += food.price * food.count
}
})
return price
},
isOK: function () {
if (!this.total_price) {
return `￥20起送`
}
if (this.total_price > 0 && this.total_price < this.seller.minPrice) {
let price = this.seller.minPrice - this.total_price
return `还差${price}元起送`
}
if (this.total_price >= this.seller.minPrice) {
return `去结算`
}
},
cart_list: function () {
return this.foodList.filter(function (food) {
if (food.count) {
return food
}
})
}
},
watch: {
cart_list: function (newvalue, oldvalue) {
this.cartScroll.refresh()
}
},
methods: {
show () {
console.log(this.cart_list)
},
empty_cart () {
this.cart_list.forEach(food => {
food.count = 0
})
this.isShow = false
},
_initScroll () {
this.cartScroll = new BetterScroll(this.$refs['cart-warpper'], {
probeType: 3,
click: true
})
},
toggle_Cart () {
if (this.cart_list.length > 0) {
this.isShow = !this.isShow
}
},
hide_mask () {
this.isShow = false
},
pay () {
if (this.total_price > this.seller.minPrice) {
this.alertShow = true
}
}
},
mounted () {
this.$nextTick(this._initScroll())
}
}



