export default {
  props: {
    food: {}
  },
  methods: {
    decrease (food) {
      if (food.count) {
        food.count--
      }
    },
    add (event, food) {
      // console.log(event.target.getBoundingClientRect())
      if (!food.count) {
        this.$set(food, 'count', 1)
      } else {
        this.food.count++
      }
    }
  }
}



